.resource-show-container {
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: center;
  padding: 0 3rem;

  .toggle-edit-button {
    width: 100%;
    max-width: 20rem;

    &.edit-mode-on {
      color: $graylighter;
      background-color: $graydarker;
      svg {
        color: $graylighter;
      }
    }

    &.edit-mode-off {
      color: $graydarker;
    }
  }

  & > .loading-spinner {
    padding: 3rem 0;
    font-size: 10rem;
  }

  .resource-show-body {
    width: 100%;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    padding: 0 $main-margins;
    @media only screen and (max-width: 900px) {
      flex-wrap: wrap;
    }

    .resource-show-section-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      min-height: 3rem;

      h2 {
        font-size: 1.3rem;
        display: inline-block;
      }
    }

    & > div > div {
      margin-bottom: 2rem;

      &.rating-stars {
        margin-bottom: 0;
      }
    }

    .resource-show-main {
      min-height: 63vh;
      width: 68%;
      padding-top: 1rem;
      padding-right: 2%;
      border-right: 1px solid $graylighter;
      box-sizing: border-box;

      @media only screen and (max-width: 900px) {
        width: 100%;
        padding-right: 0;
        border-right: none;
      }
    }

    .resource-show-aside {
      width: 32%;
      padding-top: 1rem;
      padding-left: 2%;
      box-sizing: border-box;
      @media only screen and (max-width: 900px) {
        width: 100%;
        padding-left: 0;
      }
    }
  }
}
