.reviews-index-container {
  .reviews-average-rating {
    margin-bottom: 1rem;
  }

  .reviews-index {
    .reviews-index-item {
      padding-top: 0.5rem;
      border-top: 1px solid $graylighter;
      margin-bottom: 0.5rem;

      .review-details {
        color: $graylight;
        font-size: 0.8rem;
        font-weight: 700;
        margin-bottom: 0.4rem;

        .review-attribution {
          margin-right: 0.2rem;
        }

        .review-interactions {
          & > span {
            margin-right: 0.7rem;
          }
        }
      }
    }
  }
}
